import { Fragment, useState } from "react";
import { Dialog, RadioGroup, Transition } from "@headlessui/react";
import { XMarkIcon } from "@heroicons/react/24/outline";
import { useEffect } from "react";
import { useRef } from "react";
import { BsChevronCompactLeft, BsChevronCompactRight } from "react-icons/bs";

function classNames(...classes) {
  return classes.filter(Boolean).join(" ");
}

export default function AdminCartModel({ showModal, order, tryAction }) {
  const [open, setOpen] = useState(false);
  const cancelButtonRef = useRef(null);
  const [currentIndex, setCurrentIndex] = useState(0);
  const prevSlide = () => {
    const isFirstSlide = currentIndex === 0;
    const newIndex = isFirstSlide ? order.items.length - 1 : currentIndex - 1;
    setCurrentIndex(newIndex);
  };
  const nextSlide = () => {
    const isLastSlide = currentIndex === order.items.length - 1;
    const newIndex = isLastSlide ? 0 : currentIndex + 1;
    setCurrentIndex(newIndex);
  };

  useEffect(() => {
    if (showModal) {
      setOpen(true);
    } else {
      setOpen(false);
    }
  }, [showModal]);

  return (
    <Transition.Root show={open} as={Fragment}>
      <Dialog as="div" className="relative z-10" onClose={setOpen}>
        <Transition.Child
          as={Fragment}
          enter="ease-out duration-300"
          enterFrom="opacity-0"
          enterTo="opacity-100"
          leave="ease-in duration-200"
          leaveFrom="opacity-100"
          leaveTo="opacity-0"
        >
          <div className="fixed inset-0 hidden bg-gray-500 bg-opacity-75 transition-opacity md:block" />
        </Transition.Child>

        <div className="fixed inset-0 z-10 overflow-y-auto">
          <div className="flex min-h-full items-stretch justify-center text-center md:items-center md:px-2 lg:px-4">
            <Transition.Child
              as={Fragment}
              enter="ease-out duration-300"
              enterFrom="opacity-0 translate-y-4 md:translate-y-0 md:scale-95"
              enterTo="opacity-100 translate-y-0 md:scale-100"
              leave="ease-in duration-200"
              leaveFrom="opacity-100 translate-y-0 md:scale-100"
              leaveTo="opacity-0 translate-y-4 md:translate-y-0 md:scale-95"
            >
              <Dialog.Panel className="flex w-full transform text-left text-base transition md:my-8 md:max-w-2xl md:px-4 lg:max-w-4xl">
                {
                  <div className="relative flex w-full items-center overflow-hidden bg-white px-4 pb-8 pt-14 shadow-2xl sm:px-6 sm:pt-8 md:p-6 lg:p-8">
                    <button
                      type="button"
                      ref={cancelButtonRef}
                      className="absolute right-4 top-4 text-gray-400 hover:text-gray-500 sm:right-6 sm:top-8 md:right-6 md:top-6 lg:right-8 lg:top-8"
                      onClick={() => {
                        setOpen(false);
                        tryAction();
                      }}
                    >
                      <span className="sr-only">Close</span>
                      <XMarkIcon className="h-6 w-6" aria-hidden="true" />
                    </button>

                    <div className="grid w-full grid-cols-1 items-start gap-x-6 gap-y-8 sm:grid-cols-12 lg:gap-x-8 ">
                      <div className="aspect-h-3 aspect-w-2 overflow-hidden rounded-lg bg-gray-100 sm:col-span-4 lg:col-span-5">
                        <img
                          src={order.items[currentIndex].product.thumbnail}
                          alt={order.items[currentIndex].product.title}
                          className="object-cover object-center"
                        />
                      </div>
                      <div className="sm:col-span-8 lg:col-span-7">
                        <h2 className="text-2xl font-bold text-gray-900 sm:pr-12">
                          {order.items[currentIndex].product.title}
                        </h2>

                        <section
                          aria-labelledby="information-heading"
                          className="mt-2"
                        >
                          <h3 id="information-heading" className="sr-only">
                            Product information
                          </h3>
                        </section>

                        <section
                          aria-labelledby="options-heading"
                          className="mt-10"
                        >
                          <h3 id="options-heading" className="sr-only">
                            Product options
                          </h3>

                          <form>
                            <div className="grid grid-cols-1 gap-x-6 gap-y-8 sm:grid-cols-6 ">
                              {/* Colors */}

                              {order.items[currentIndex].color && (
                                <div>
                                  <h4 className="text-sm font-medium text-gray-900">
                                    Color
                                  </h4>

                                  <RadioGroup className="mt-4">
                                    <RadioGroup.Label className="sr-only">
                                      Choose a color
                                    </RadioGroup.Label>
                                    <span className="flex items-center space-x-3">
                                      <RadioGroup.Option
                                        key={
                                          order.items[currentIndex].color.name
                                        }
                                        value={order.items[currentIndex].color}
                                        className={`relative -m-0.5 flex items-center justify-center rounded-full p-0.5 focus:outline-none ${order.items[currentIndex].color.selectedClass} ring-2  ring-offset-1`}
                                      >
                                        <RadioGroup.Label
                                          as="span"
                                          className="sr-only"
                                        >
                                          {order.items[currentIndex].color.name}
                                        </RadioGroup.Label>
                                        <span
                                          aria-hidden="true"
                                          className={classNames(
                                            order.items[currentIndex].color
                                              .class,
                                            "h-10 w-10 rounded-full border border-black border-opacity-10"
                                          )}
                                        />
                                      </RadioGroup.Option>
                                    </span>
                                  </RadioGroup>
                                </div>
                              )}

                              {/* Sizes */}

                              {order.items[currentIndex].size && (
                                <div className="">
                                  <div className="flex items-center justify-between">
                                    <h4 className="text-sm font-medium text-gray-900">
                                      Size
                                    </h4>
                                  </div>

                                  <RadioGroup className="mt-4">
                                    <div className="grid grid-cols-2 gap-0">
                                      <RadioGroup.Option
                                        key={
                                          order.items[currentIndex].size.name
                                        }
                                        value={order.items[currentIndex].size}
                                        className="group relative flex items-center justify-center rounded-md border py-3 px-5 text-sm font-medium uppercase ring-2 ring-indigo-500 hover:bg-gray-50 focus:outline-none sm:flex-1"
                                      >
                                        <>
                                          <RadioGroup.Label as="span">
                                            {
                                              order.items[currentIndex].size
                                                .name
                                            }
                                          </RadioGroup.Label>
                                        </>
                                      </RadioGroup.Option>
                                    </div>
                                  </RadioGroup>
                                </div>
                              )}

                              <div className="">
                                <h4 className="text-sm font-medium text-gray-900">
                                  Quantity
                                </h4>
                                <RadioGroup className="mt-4">
                                  <div className="grid grid-cols-2 gap-0">
                                    <RadioGroup.Option
                                      key={order.items[currentIndex].quantity}
                                      value={order.items[currentIndex].quantity}
                                      className="group relative flex items-center justify-center rounded-md border py-3 px-6 text-sm font-medium uppercase ring-2 ring-indigo-500 hover:bg-gray-50 focus:outline-none sm:flex-1"
                                    >
                                      <>
                                        <RadioGroup.Label as="span">
                                          {order.items[currentIndex].quantity}
                                        </RadioGroup.Label>
                                      </>
                                    </RadioGroup.Option>
                                  </div>
                                </RadioGroup>
                              </div>

                              <div className="">
                                <h4 className="text-sm font-medium text-gray-900">
                                  Price
                                </h4>
                                <RadioGroup className="mt-4">
                                  <div className="grid grid-cols-2 ">
                                    <RadioGroup.Option
                                      key={
                                        order.items[currentIndex].product
                                          .discountPrice
                                      }
                                      value={
                                        order.items[currentIndex].product
                                          .discountPrice
                                      }
                                      className="group relative flex items-center justify-center rounded-md border py-3 px-6 text-sm font-medium uppercase ring-2 ring-indigo-500 hover:bg-gray-50 focus:outline-none sm:flex-1"
                                    >
                                      <>
                                        <RadioGroup.Label as="span">
                                          $
                                          {
                                            order.items[currentIndex].product
                                              .discountPrice
                                          }
                                        </RadioGroup.Label>
                                      </>
                                    </RadioGroup.Option>
                                  </div>
                                </RadioGroup>
                              </div>

                              <div className=" col-span-2 ">
                                <h4 className="text-sm font-medium text-gray-900">
                                  Total Amount
                                </h4>
                                <RadioGroup className="mt-4 ml-6 ">
                                  <div className="grid grid-cols-2  ">
                                    <RadioGroup.Option
                                      key={order.totalAmount}
                                      value={order.totalAmount}
                                      className="group relative flex items-center justify-center rounded-md border py-3 px-4 text-sm font-medium uppercase ring-2 ring-indigo-500 hover:bg-gray-50 focus:outline-none sm:flex-1"
                                    >
                                      <>
                                        <RadioGroup.Label as="span">
                                          ${order.totalAmount}
                                        </RadioGroup.Label>
                                      </>
                                    </RadioGroup.Option>
                                  </div>
                                </RadioGroup>
                              </div>
                            </div>
                            <div className="grid grid-cols-1 gap-x-6 gap-y-4 sm:grid-cols-6  pt-8">
                              <div className=" col-span-6 pl-[7rem]">
                                <h2 className="text-xl font-medium text-gray-900">
                                  Shipping Details
                                </h2>
                              </div>

                              <div className=" col-span-3  ">
                                <h4 className="text-sm font-medium text-gray-900">
                                  Customer Name:
                                </h4>
                              </div>
                              <div className=" col-span-3  ">
                                <h5>{order.selectedAddress.name}</h5>
                              </div>

                              <div className=" col-span-3  ">
                                <h4 className="text-sm font-medium text-gray-900">
                                  Customer Phone no.
                                </h4>
                              </div>
                              <div className=" col-span-3  ">
                                <h5>{order.selectedAddress.phone}</h5>
                              </div>

                              <div className=" col-span-3  ">
                                <h4 className="text-sm font-medium text-gray-900">
                                  Shipping Address:
                                </h4>
                              </div>
                              <div className=" col-span-3  ">
                                <h5>
                                  {order.selectedAddress.street},{" "}
                                  {order.selectedAddress.city}-
                                  {order.selectedAddress.pinCode},{" "}
                                  {order.selectedAddress.state}
                                  {}
                                </h5>
                              </div>
                            </div>
                          </form>
                        </section>
                      </div>

                      {order.items.length > 1 ? (
                        <div>
                          <div className="absolute top-[50%] -translate-x-0 translate-y-[-50%] left-5 text-2xl rounded-full p-2 bg-black/20 text-white cursor-pointer">
                            <BsChevronCompactLeft
                              onClick={prevSlide}
                              size={30}
                            />
                          </div>

                          <div className="absolute top-[50%] -translate-x-0 translate-y-[-50%] right-5 text-2xl rounded-full p-2 bg-black/20 text-white cursor-pointer">
                            <BsChevronCompactRight
                              onClick={nextSlide}
                              size={30}
                            />
                          </div>
                        </div>
                      ) : null}
                    </div>
                  </div>
                }
              </Dialog.Panel>
            </Transition.Child>
          </div>
        </div>
      </Dialog>
    </Transition.Root>
  );
}
